@import url(../App.css);

.body {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: var(--color-body);
    background-image: url(../assets/ima_login.png);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
}

.contentLogin {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    min-width: 380px;
    max-width: 380px;
    min-height: 450px;
    backdrop-filter: blur(20px);
    border-radius: 22px;
    /* border: 1px solid white; */
}

.contentLogin::before {
    content: "";
    position: absolute;
    top: -2px; left: -2px; right: -2px; bottom: -2px;
    border-radius: 24px;
    padding: 2px;
    background: linear-gradient(var(--color-login-grey), transparent); 
    -webkit-mask: 
      linear-gradient(#fff 0 0) content-box, 
      linear-gradient(#fff 0 0); 
    -webkit-mask-composite: destination-out;
    mask-composite: exclude;
    pointer-events: none; 
}

.contentLogin p {
    font-size: 14px;
}

.boximage {
    position: fixed;
    top: -10%;
    background-color: var(--color-white);
    padding: 20px;
    border-radius: 24px;
    z-index: 1000000000000000;
    width: 50px;
    height: 50px;
}

.image {
    width: auto;
    height: 50px;
}

.welcome {
    text-align: center;
    color: var(--color-white);
    margin-bottom: 40px;
}

.boxInput {
    display: flex;
    gap: 16px;
    flex-direction: column;
    align-items: center;
}

.boxInput input,
.boxInput button {
    border: 1px solid var(--color-button-grey);
    background-color: var(--color-sidebar-card);
    padding: 10px;
    width: 300px;
    border-radius: 12px;
    color: var(--color-white);
    outline: none;
    text-align: center;
    transition: border 0.3s ease;
}

.boxInput input:hover {
    border: 1px solid var(--color-login-grey);
}


.boxInput button {
    margin-top: 40px;
    background-color: var(--color-button-green);
    width: 340px;
    transition: background-color 0.3s ease;
}

.boxInput button:hover {
    background-color: var(--color-button-green-hover);
}

